<template>
  <div>
    <h1 class="brand">Worldaria</h1>
    <div class="start">
      <div class="left">
        <p>In Game mode you have 5 attempts for each country. If you mark right, you will need to guess the capital of that country. Each right country and capital will give you 1 point. After 5 wrong attempts you will lose 1 point</p>
        <router-link to="/game" tag="button" class="btn btn--start">Play All</router-link>
        <p class="mb0">Play countries by their population</p>
        <div class="buttons">
          <router-link :to="{ name: 'Game', params: { sort: 5000000 }}" tag="button" class="btn btn--start">More 5 m</router-link>
          <router-link :to="{ name: 'Game', params: { sort: 10000000 }}" tag="button" class="btn btn--start">More 10 m</router-link>
          <router-link :to="{ name: 'Game', params: { sort: 50000000 }}" tag="button" class="btn btn--start">More 50 m</router-link>
        </div>
      </div>
      <div class="right">
        <p>In Learn mode you have unlimited attempts. After 5 wrong attempts the right country will be accentuated</p>
        <router-link to="/learn/full" tag="button" class="btn btn--start">Learn All</router-link>
        <div class="buttons">
          <router-link to="/learn/continent" tag="button" class="btn btn--start">Continents</router-link>
          <router-link to="/learn/countries" tag="button" class="btn btn--start">Regions</router-link>
          <router-link to ="/learn/details" tag="button" class="btn btn--start">Details</router-link>
          <router-link to ="/learn/history" tag="button" class="btn btn--start">History</router-link>
        </div>
      </div>
  </div>
  </div>
</template>
<script>
export default {
  beforeCreate: function() {
    document.body.className = 'start';
  }
}
</script>
<style scoped>

.start {
  min-height: calc(100vh - 70px);
  display: flex;
  color: #fff;
}
.brand {
  height: 70px;
  line-height: 70px;
  margin: 0;
  text-align: center;
  font-family: 'Michroma', sans-serif;
  font-size: 48px;
  text-transform: uppercase;
  letter-spacing: -6px;
  background-color: rgba(0,0,0,0.5);
  color: #fff;
}
.left, .right {
  width: 50%;
  padding: 20px 50px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  line-height: 1.6;
  font-size: 20px;
}
.mb0 {
  margin-bottom: 0;
}
.buttons {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}
.buttons .btn {
  margin-top: 20px;
}
@media screen and (max-width: 650px) {
  .start {
    flex-wrap: wrap;
  }
  .left, .right {
    width: 100%;
    padding: 20px;
    font-size: 18px;
  }
  .right {
    padding: 20px 20px 40px;
  }
  .brand {
    font-size: 36px;
  }
}
</style>


