<template>
  <div>
    <div class="modal-backdrop"></div>
    <div class="modal">
      <div class="modal-content">
        <div v-if="mode === 'learn' && name === 'full'">
          <div class="status learn">
            <img src="../assets/images/correct-symbol.svg" height="24" width="24" />
            Congratulations!
          </div>
          <div class="learnt">
            You have learnt all countries. You are now ready for exciting game!
          </div>
        </div>
        <div v-else-if="mode === 'learn' && name !== 'full'">
          <div class="status learn">
            <img src="../assets/images/correct-symbol.svg" height="24" width="24" />
            Congratulations!
          </div>
          <div class="learnt">
            You have learnt all {{ fullName }}.
          </div>
        </div>
        <div v-else>
          <div class="status win">
            <img src="../assets/images/correct-symbol.svg" height="24" width="24" />
            Congratulations! You win!
          </div>
          <div class="score">
            Your score: 
            <span :class="score >= best_score ? 'green' : 'tomato'">{{ score }}</span>
          </div>
          <div class="best-score">
            Your best score: {{ best_score }}
          </div>
          <div v-if="leaderboard.length === 0" class="send-nick">
            <div class="error">
              <span class="error-text" v-if="nickname.length > 19">* Name length - less 20 letters</span>
              <span class="error-text" v-else-if="smallName">* Name length - more 2 letters</span>
            </div>
            <input @input="checkNick" type="text" v-model="nickname" placeholder="Enter nickname">
            <button @click="sendResults" class="btn">Send</button>
          </div>
          <div v-else class="leaderboard">
            <h1>Leaderboard</h1>
            <ol>
              <li v-for="(user, id) in leaderboard" :key="id" :class="user.name === nickname ? 'leader' : ''">
                <mark>{{ user.name }}</mark>
                <span>{{ user.score }}</span>
              </li>
            </ol>
          </div>
          <div v-if="sending" class='preloader'>
            <div class='preloader-dots'>
              <div class='dot'></div>
              <div class='dot'></div>
              <div class='dot'></div>
              <div class='dot'></div>
              <div class='dot'></div>
            </div>
          </div>
        </div>
        <router-link to="/" tag="button" class="btn">Home</router-link>
      </div>
    </div>
  </div>
</template>
<script>
import Vue from 'vue'

export default Vue.component('modal', {
  data () {
    return {
      best_score: 0,
      nickname: '',
      leaderboard: [],
      sending: false,
      smallName: false
    }
  },
  created () {
    if (this.mode === 'game') {
      this.getBestScore()
    }
  },
  props: {
    mode: {
      type: String,
      required: false
    },
    reason: {
      type: String,
      required: true
    },
    score: {
      type: Number,
      default: 0
    }
  },
  computed: {
    name () {
      return this.$route.params.name
    },
    fullName () {
      switch (this.name) {
        case 'full':
          return 'countries'
        case 'europe':
          return 'European countries'
        case 'asia':
          return 'Asian countries'
        case 'africa':
          return 'African countries'
        case 'na':
          return 'North American countries'
        case 'sa':
          return 'South American countries'
        case 'oceania':
          return 'Oceanian countries'
        default:
          return 'regions'
      }
    }
  },
  methods: {
    getBestScore () {
      if (localStorage.getItem('best_score')) {
        if (this.score > +localStorage.getItem('best_score')) {
          this.best_score = this.score
          localStorage.setItem('best_score', this.best_score)
        } else {
          this.best_score = +localStorage.getItem('best_score')
        }
      } else {
        this.best_score = this.score
        localStorage.setItem('best_score', this.best_score)
      }
    },
    checkNick () {
      if (this.nickname.length > 2) {
        this.smallName = false
      }
      if (this.nickname.length > 20) {
        this.nickname = this.nickname.slice(0, -1)
      }
    },
    sendResults () {
      if (this.nickname.length < 3) {
        this.smallName = true
        return
      } else if (this.nickname.length > 2 && this.nickname.length < 20) {
        this.sending = true
        this.$http.post('/postScore', { name: this.nickname, score: this.score })
          .then(resp => {
            this.sending = false
            this.leaderboard = [...resp.body]
          })
      }
    }
  }
})
</script>
<style scoped lang="scss">
::-webkit-scrollbar {
    width: 5px;
}

::-webkit-scrollbar-track {
    background: #f1f1f1; 
}

::-webkit-scrollbar-thumb {
    background: #888;
    border-radius: 5px; 
}

::-webkit-scrollbar-thumb:hover {
    background: #555; 
}
.modal-backdrop {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  z-index: 1000;
  background-color: rgba(0, 0, 0, 0.5);
}
.modal {
  position: fixed;
  width: 100%;
  max-width: 500px;
  max-height: 100vh;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  margin: 0 auto;
  padding-bottom: 3px;
  z-index: 1051;
  background: #fff;
  overflow-y: auto;
  box-shadow: 0px 0px 10px 5px rgba(0, 0, 0, 0.3);
}
.modal::before {
  background: linear-gradient(to right, #9BFFE6 0%, #A3CCFF 100%);
  content: "";
  display: block;
  height: 3px;
  width: 100%;
}
@media screen and (max-width: 500px) {
  .modal {
    height: 100vh;
  }
}
.modal-content {
  height: auto;
  position: relative;
  top: 3px;
  left: 0;
  right: 0;  
  z-index: 1052;
  padding: 30px 15px 30px 20px;
  background: #fff;
  font-size: 24px;
}
.status {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
.status img {
  margin-right: 10px;
}
.win {
  color: #3a87ad;
}
.green {
  color: #91DC5A;
}
.tomato {
  color: tomato;
}
.score, .best-score {
  margin-top: 10px;
}
.best-score {
  margin-bottom: 20px;
}
.learnt {
  padding: 20px;
}
img {
  animation: img_scale 1s infinite;
}
@keyframes tiltup {
  0% { 
    bottom: -400px;
    opacity: 0; 
    transform: rotateX(-90deg);
    border-top: solid 40px; 
  } 
  30% {
    opacity: 0;
  }
  100% {
    bottom: 0;
  }
}
@keyframes img_scale {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}
.send-nick {
  position: relative;
  padding: 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}
.error {
  position: absolute;
  top: -5px;
  left: 20px;
  font-size: 14px;
  color: tomato;
}
.error-text {
  opacity: 0;
  animation: error 0.5s forwards;
}
@keyframes error {
  100% {
    opacity: 1;
  }
}
input {
  width: 200px;
  height: 50px;
  border-radius: 5px;
  border: 2px solid #7CCCB8;
  padding: 0 20px;
  outline: none;
  font-family: inherit;
  transition: box-shadow 0.2s;
}
input:focus {
  box-shadow: 0px 0px 10px #3E665C;
}

.leaderboard {
  margin-top: 20px;
  padding: 0 40px;
  transform-origin: top;
  animation: show-leaderboard 0.5s;
}
@media screen and (max-width: 500px) {
  .leaderboard {
    padding: 0;
  }
}
@keyframes show-leaderboard {
  0% {
    transform: scaleY(0);
  }
  100% {
    transform: scaleY(1);
  }
}
h1 {
  margin: 0;
  font-size: 22px;
  padding: 10px 0;
  color: #e1e1e1;
  background: #3a404d;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}
ol {
  margin-top: 0;
  padding: 0;
  counter-reset: leaderboard;
  list-style-type: none;

  li {
    position: relative;
    z-index: 1;
    font-size: 14px;
    counter-increment: leaderboard;
    padding: 18px 10px 18px 50px;
    cursor: pointer;
    backface-visibility: hidden;
    transform: translateZ(0) scale(1.0, 1.0);
    text-align: left;


    &::before {
      content: counter(leaderboard);
      position: absolute;
      z-index: 2;
      top: 15px;
      left: 15px;
      width: 20px;
      height: 20px;
      line-height: 20px;
      color: #1f332e;
      background: #fff;
      border-radius: 20px;
      text-align: center;
    }

    mark {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      padding: 18px 10px 18px 50px;
      margin: 0;
      background: none;
      color: #fff;

      &::before, &::after {
        content: '';
        position: absolute;
        z-index: 1;
        bottom: -11px;
        left: -9px;
        border-top: 10px solid #2e4c45;
        border-left: 10px solid transparent;
        transition: all .1s ease-in-out;
        opacity: 0;
      }

      &::after {
        left: auto;
        right: -9px;
        border-left: none;
        border-right: 10px solid transparent;
      }
    }

    span {
      position: relative;
      z-index: 2;
      display: block;
      text-align: right;
      font-weight: bold;
      color: #fff;
    }

    &::after {
      content: '';
      position: absolute;
      z-index: 1;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: #fa6855;
      box-shadow: 0 3px 0 rgba(0, 0, 0, .08);
      // transform: scaleX(1.06) scaleY(1.03);
      transition: all .3s ease-in-out;
      opacity: 0;
    }

    &:nth-child(1) {
      background: #7cccb8;
      &::after {
        background: #7cccb8;
      }
    }


    &:nth-child(2) {
      background: #6cb2a1;
      &::after {
        background: #6cb2a1;
        box-shadow: 0 2px 0 rgba(0, 0, 0, .08);
      }

      & mark {
        &::before, &::after {
          border-top: 6px solid #2e4c45;
          bottom: -7px;
        }
      }
    }

    &:nth-child(3) {
      background: #5d998a;
      &::after {
        background: #5d998a;
        box-shadow: 0 1px 0 rgba(0, 0, 0, .11);
      }

      & mark {
        &::before, &::after {
          border-top: 2px solid #2e4c45;
          bottom: -3px;
        }
      }
    }

    &:nth-child(4) {
      background: #4d7f73;
      &::after {
        background: #4d7f73;
        box-shadow: 0 -1px 0 rgba(0, 0, 0, .15);
      }

      & mark {
        &::before, &::after {
          top: -7px;
          bottom: auto;
          border-top: none;
          border-bottom: 6px solid #2e4c45;
        }
      }
    }

    &:nth-child(5) {
      background: #3e665c;
      border-radius: 0 0 10px 10px;
      &::after {
        background: #3e665c;
        box-shadow: 0 -2.5px 0 rgba(0, 0, 0, .12);
        border-radius: 0 0 10px 10px;
      }

      & mark {
        &::before, &::after {
          top: -9px;
          bottom: auto;
          border-top: none;
          border-bottom: 8px solid #2e4c45;
        }
      }
    }
  }
  
  li.leader {
    background-color: #dff2ee;
    color: #2e4c45;

    &:before {
      background-color: #2e4c45;
      color: #fff;
    }

    & mark, & span {
      color: inherit;
      transition: color 0.2s;
    }
  }
  
  li.leader:hover {
    & mark, & span {
      color: #fff;
    }
  }

  li:hover {
    z-index: 2;
    overflow: visible;

    &::after {
      opacity: 1;
      transform: scaleX(1.06) scaleY(1.03);
    }

    & mark {
      &::before, &::after {
        color: #fff;
        opacity: 1;
        transition: all .35s ease-in-out;
      }
    }
  }
}

.preloader {
  background: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}
.preloader-dots {
  height: 50px;
  width: 80px;
}

.preloader-dots .dot {
  display: inline-block;
  background: #666;
  height: 8px;
  width: 8px;
  opacity: 0.3;
  border-radius: 50%;
  animation: moveit 1s infinite;
}

.dot:nth-child(2) {
  animation-delay: .1s;
}

.dot:nth-child(3) {
  animation-delay: .2s;
}

.dot:nth-child(4) {
  animation-delay: .3s;
}

.dot:nth-child(5) {
  animation-delay: .4s;
}

@keyframes moveit {
  0% {
    transform: translateY(0px);
  }
  35% {
    transform: translateY(0px);
    opacity: 0.3;
  }
  50% {
    transform: translateY(-20px);
    opacity: 0.8;
  }
  70% {
    transform: translateY(3px);
    opacity: 0.8;
  }
  85% {
    transform: translateY(-3px);
  }
}
</style>


